.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
    background-color: #89891c;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color:white;
    font-size: 38px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: white;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border:none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: red;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid red;
    border-radius: 75px;
    color: white;
    font-size: 20px;
    font-weight: 500;
    background: #868629;
    cursor: pointer;
}
.nav-login-cart button:active{
    background: red;
    color: white;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display:flex;
    justify-content: center;
    margin-top:-35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}
.nav-dropdown{
    display: none;
}
@media(max-width:1280px){
    .navbar{
        padding:10px 50px;
    }
    .nav-logo img{
        width: 40px;
    }
    .nav-logo p{
        font-size: 26px;
    }
    .nav-menu{
       gap: 30px;
       font-size: 18px; 
    }
    .nav-login-cart{
        gap: 30px;
    }
    .nav-login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }
}
@media(max-width:1024px){
    .navbar{
        padding: 10px 30px;
           }
    .nav-menu{
       gap: 25px;
       font-size: 14px; 
    }
    .nav-login-cart button{
        width: 80px;
        height: 35px;
        font-size: 14px;
    }
    .nav-login-cart img{
        width: 30px;
    }
    .nav-cart-count{
        width: 18px;
        height: 18px;
    }
}
@media(max-width:800px){
    .navbar{
        padding:10px 0px;
            }
    .nav-dropdown{
        display: block;
        width: 30px;
        rotate: -90deg;
        transition: 0.5s;
        }
    .nav-menu{
        display: none;
        position: absolute;
        justify-content: center;
        height: 90px;
        width: 100%;
        top: 50px;
        background-color: #89891c;
    }
    .nav-menu-visible{
        display: flex;
    }
    .nav-dropdown.open{
        transform: rotate(90deg);
    }
} 
@media(max-width:500px){
    .navbar{
        width: 8px 0px;
        gap: 0;

    }
    .nav-logo{
        transform: scale(0.8);
    }
    .nav-menu{
        height: 55px;
        top: 50px;
    }
    .nav-login-cart{
        transform:scale(0.8); 
    }
}

